import s from './Post.module.css';
import React from "react";

const Post = React.memo(props => {
        return (

            <div>
                <div className={s.item}>
                    <img
                        src='data:image/jpeg;base64,'></img>
                    {props.message}
                    <div><span>like {props.like}</span></div>
                </div>
            </div>
        );
    }
);


export default Post;
